/* # <-- 아이디 지정 */
#auth-wrapper {
    display: flex;
    /* 왼쪽에서 오른쪽으로 배치 */
}

/* auth-wrapper 아래의 auth-side-image에 대해 작업함을 표시 */
#auth-wrapper .auth-side-image {
    width: 830px;
    min-width: 830px;
    min-height: calc(1024px - 80px);
    height: 100vh;

    background-image: url(src/assets/images/auth-side-image.png);
    background-repeat: no-repeat;
    background-size: cover;
    /* 
        cover: 영역에 맞춰 이미지 크기 조절
        contain: 이미지 전체가 나오도록 영역에 맞춰 이미지 크기 조절
    */
}

#auth-wrapper .auth-box {
  flex: 1;

  display: flex;
  align-items: center;
  justify-content: center;

  box-sizing: border-box;
  padding-left: 100px;
  padding-right: 130px;
  min-width: 610px;
}

#auth-wrapper .divider {
  border-top: 1px solid var(--Lightmode-Grey3);
  width: 64px;
}

.sns-button.kakao {
  background-image: url('/src/assets/images/kakao.png');
}

.sns-button.naver {
  background-image: url('/src/assets/images/naver.png');
}