#diary-detail-wrapper {
    margin: 0 auto;
    width: 1180px;
}

#diary-detail-wrapper .detail-container {
    display: flex;
    flex-direction: column;
    gap: 40px;

    border-radius: 10px;
    padding: 24px;

    background-color: var(--White);
}

#diary-detail-wrapper .contents-container {
    display: flex;
    flex-direction: column;
    gap: 24px;
}

#diary-detail-wrapper .detail-title {
    color: var(--Lightmode-Grey);

    font-size: 24px;
    font-weight: 700;
}

#diary-detail-wrapper .content-box {
    display: flex;
    align-items: center;
    gap: 16px;
}

#diary-detail-wrapper .content-box .title {
    width: 50px;

    color: var(--Lightmode-Grey);

    font-size: 16px;
    font-weight: 700;
}

#diary-detail-wrapper .content-box .content {
    display: flex;
    align-items: center;
    gap: 2px;

    color: var(--Lightmode-Grey);

    font: 14px;
    font-weight: 400;
    line-height: 20px;
}

#diary-detail-wrapper .button-box {
    display: flex;
    justify-content: end;
    gap: 20px;
}

#diary-detail-wrapper .sub-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

#diary-detail-wrapper .sub-container .header {
    display: flex;
    gap: 12px;
}

#diary-detail-wrapper .sub-container .sub-box {
    display: flex;
    align-items: center;
    gap: 16px;

    width: 80px;

    color: var(--Lightmode-Grey4);

    font-size: 16px;
    font-weight: 500;
}

#diary-detail-wrapper .sub-container .icon {
    width: 24px;
    height: 24px;

    background-size: 100% 100%;

    cursor: pointer;
}

#diary-detail-wrapper .sub-container .icon.empathy {
    background-image: url(src/assets/images/empathy.png);
}

#diary-detail-wrapper .sub-container .icon.empathy-empty {
    background-image: url(src/assets/images/empathy-empty.png);
}

#diary-detail-wrapper .sub-container .icon.comment {
    background-image: url(src/assets/images/comment.png);
}

#diary-detail-wrapper .sub-container .body {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

#diary-detail-wrapper .comment-box {
    display: flex;
    flex-direction: column;
    gap: 10px;

    border-bottom: 1px solid var(--Lightmode-Grey3);
    padding: 12px;
}

#diary-detail-wrapper .comment-box .title-box {
    display: flex;
    align-items: center;
    gap: 8px;
}

#diary-detail-wrapper .comment-box .title-box .title {
    color: var(--Lightmode-Grey2);

    font-size: 14px;
    font-weight: 700;
}

#diary-detail-wrapper .comment-box .title-box .divider {
    align-self: stretch;
    border-left: 1px solid var(--Lightmode-Grey3);
}

#diary-detail-wrapper .comment-box .title-box .write-date {
    color: var(--Lightmode-Grey4);

    font-size: 14px;
    font-weight: 400;
}

#diary-detail-wrapper .edit-delete-box {
    display: flex;
    gap: 8px;
}

#diary-detail-wrapper .edit-delete-box .edit-delete {
    color: var(--Lightmode-Grey4);

    font-size: 14px;
    font-weight: 400;

    cursor: pointer;
}

#diary-detail-wrapper .edit-delete-box .edit-delete:hover {
    color: var(--Lightmode-Grey);
    font-weight: 600;
}

#diary-detail-wrapper .comment-box .comment {
    color: var(--Lightmode-Grey);

    font-size: 16px;
    font-weight: 400;
    line-height: 125%;

    white-space: pre-wrap;
}

#diary-detail-wrapper .sub-container .footer {
    display: flex;
    gap: 12px;

    height: 120px;
}

#diary-detail-wrapper .sub-container .footer textarea {
    flex: 1;

    outline: none;
    resize: none;

    border-radius: 6px;
    border: 1px solid var(--Lightmode-Grey3);
    padding: 8px 16px;

    color: var(--Lightmode-Grey);

    font-family: Inter;
    font-size: 16px;
    font-weight: 400;
    line-height: 175%;
}